import React from "react";
import { languages, skills, icons } from "../../skills.js";

function SkillEntry(props) {

    function convertToBar(num, icon) {

        if (typeof(num) !== 'number') {
            return num
        }

        let bar = "";
        for (let i = 0; i < (num / 2); i++) {
            bar = bar + icon;
        }
        return bar
    }

    return (
        <li>
            {props.name}
            <span className="left-align">
                {convertToBar(props.expertise, props.icon)}
            </span>
        </li>
    )
}

function HomeSkills() {
    return (
        <section id="home-skills">
            <div className="home-skills">
                <h2>Skills</h2>
                <div className="row">
                    <div className="col-md-6 skill-col">
                        <div>
                            <h4>Languages</h4>
                            {languages.map((item, index)=>{
                                return <SkillEntry key={index} name={item.name} expertise={item.expertise} icon={icons.icon_language}/>
                            })}
                        </div>
                    </div>

                    <div className="col-md-6 skill-col">
                        <div>
                            <h4>Technical Skills</h4>
                            {skills.map((item, index)=>{
                                return <SkillEntry key={index} name={item.name} expertise={item.expertise} icon={icons.icon_skill}/>
                            })}
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default HomeSkills;