import React, { useEffect } from "react";
import { Switch, Route, useLocation } from "react-router-dom";

import NavBar from "./general/NavBar.jsx";
import HomePage from "./home/HomePage.jsx";
import ProjectPage from "./projects/ProjectPage.jsx";
import WIPPage from "./wip/WIPPage.jsx";
import Footer from "./general/Footer.jsx";
import ProjectSpotlight from "./projects/ProjectSpotlight.jsx";

function App() {

  const { pathname, hash } = useLocation()

  useEffect(() => {

        // if not a hash link scroll to top
        if(hash===''){
            window.scrollTo(0, 0)
            // document.getElementById("navbarSupportedContent").classList.remove('show');
        }
        // else scroll to id
        else{
            setTimeout(
                () => {
                    const id = hash.replace('#', '');
                    const element = document.getElementById(id);
                    if (element) {
                        const y = element.getBoundingClientRect().top + window.pageYOffset - 70;
                        window.scrollTo({top: y, behavior: 'smooth'});
                    }
                },
                10
            );
        }
    }, [pathname, hash]) // do this on route change

  return (
    <div className="App">

        <NavBar/>

        <Switch>
          <Route path="/projects/pyqt-tetris"><ProjectSpotlight project={0}/></Route>
          <Route path="/projects/sudoku-solver"><ProjectSpotlight project={1}/></Route>
          <Route path="/projects/ar-marker-tracker"><ProjectSpotlight project={2}/></Route>
          <Route path="/projects/badminton-shuttle-tracker"><ProjectSpotlight project={3}/></Route>
          <Route path="/projects"><ProjectPage /></Route>
          <Route path="/wip"><WIPPage /></Route>
          <Route path="/"><HomePage /></Route>
        </Switch>

        <Footer/>

    </div>
  );
}

export default App;