import React from "react";
import ProjectTiles from "./ProjectTiles.jsx";
import projects from "../../projects-content.js";

function ProjectBody() {
    return (
        <div className="project-tile-grid">
            <div className="row">
                {projects.map((project, index) => {
                    return <ProjectTiles key={index} tile={project.tile}/>
                })}
            </div>
        </div>
    )
}

export default ProjectBody;