import React from "react";
import { Link } from "react-router-dom";


function NavBarDropDown(props) {

  if (props.page === "WIP") {
    return (
      <div className="dropdown-menu">
        <a className="dropdown-item" href="/wip">Action</a>
        <a className="dropdown-item" href="/wip">Another action</a>
        <a className="dropdown-item" href="/wip">Something else here</a>
        <div className="dropdown-divider"></div>
        <a className="dropdown-item" href="/wip">Separated link</a>
      </div>
    )
  } else if (props.page === "About") {
    return (
      <div className="dropdown-menu" aria-labelledby="navbarDropdown">
        <Link className="dropdown-item" to="/#home-title">Home</Link>
        <Link className="dropdown-item" to="/#home-education">Education</Link>
        <Link className="dropdown-item" to="/#home-experience">Experience</Link>
        <Link className="dropdown-item" to="/#home-skills">Skills</Link>
    </div>
    )
  } else if (props.page === "Projects") {
    return (
      <div className="dropdown-menu">
        <Link className="dropdown-item" data-toggle="collapse" data-target=".navbar-collapse.show" to="/projects/pyqt-tetris">PyQt Tetris</Link>
        <Link className="dropdown-item" data-toggle="collapse" data-target=".navbar-collapse.show" to="/projects/sudoku-solver">Sudoku Solver</Link>
        <Link className="dropdown-item" data-toggle="collapse" data-target=".navbar-collapse.show" to="/projects/ar-marker-tracker">AR Marker Tracker</Link>
        <Link className="dropdown-item" data-toggle="collapse" data-target=".navbar-collapse.show" to="/projects/badminton-shuttle-tracker">Badminton Tracker</Link> 
      </div>
    )
  }
}

function NavBarText(props) {
  const listItemClasses = "nav-item dropdown nav-collapse";
  const linkItemClasses = "nav-link navbar-text-style dropdown-toggle caret-off";

  return (
    <li className={listItemClasses}>
      <div className="btn-group">

        <Link className={linkItemClasses} to={props.navLink} data-toggle="collapse" data-target=".navbar-collapse.show">
          <button type="button" className="btn btn-navbar btn-sm">{props.navText}</button>
        </Link>
        <button type="button" id="navbarDropdown" className="btn btn-navbar dropdown-toggle dropdown-toggle-split" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            <span className="sr-only">Toggle Dropdown</span>
        </button>
        <NavBarDropDown page={props.navText} />

      </div>
      
    </li>
  );
}

function NavBarIcon(props) {
    const listItemClasses = "nav-item";
    const linkItemClasses = "nav-link navbar-icon-style";
  
    return (
      <div className={listItemClasses}>
        <a className={linkItemClasses} href={props.navLink}>
            <button className="btn btn-navbar">
              <i className={props.icon}></i>
            </button>
        </a>
      </div>
    );
}

function NavBar() {
  return (
    <nav className="navbar navbar-expand-lg navbar-dark navbar-main fixed-top">
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav">
          <NavBarText navLink="/" navText="About" />
          <NavBarText navLink="/projects" navText="Projects" />
          <NavBarText navLink="/wip" navText="WIP" />
        </ul>


        {/* TEST */}
        <div className="ml-auto nav-icon">
          <NavBarIcon navLink="https://github.com/alanx3x7" icon="fab fa-github"/>
          <NavBarIcon navLink="https://www.linkedin.com/in/alan-lai-0129" icon="fab fa-linkedin"/>
        </div>



      </div>

      {/* <div className="navbar-nav ml-auto">
        <NavBarIcon navLink="https://github.com/alanx3x7" icon="fab fa-github"/>
        <NavBarIcon navLink="https://www.linkedin.com/in/alan-lai-0129" icon="fab fa-linkedin"/>
      </div> */}
      
    </nav>
  );
}

export default NavBar;
