import React from "react";
import { Link } from "react-router-dom";

function ProjectTiles(props) {

    const backgroundStyle = {
        backgroundColor: props.tile.backgroundColor
    }

    const imgStyle = {
        width: props.tile.width,
        padding: props.tile.padding
    };

    return (
        <div className="col-md-4 col-sm-6">
            <Link className="card card-hover project-tile" to={props.tile.link} style={backgroundStyle}>
                <div className="card-img-top card-image-background">
                    <img src={props.tile.icon} className="tile-image" alt="..." style={imgStyle}/>  
                </div>

                <div className="card-body">
                    <h5 className="card-title">{props.tile.title}</h5>
                    <p className="card-text">{props.tile.description}</p>
                </div>
            </Link>
        </div>
    )
}

export default ProjectTiles;

