import React from "react";
import { Link } from "react-router-dom";

import projects from "../../projects-content.js";

function ProjectCarouselCard(props) {

    const isValid = props.prjNum !== -1;
    const cardTitle = isValid? projects[props.prjNum].carousel.name : "Coming Soon!";
    const cardDescript = isValid? projects[props.prjNum].carousel.description : "Follow to see what's next...";
    const cardImage = isValid? projects[props.prjNum].carousel.image : "../images/project/coming-soon.jpg";
    const cardLink = isValid? projects[props.prjNum].tile.link : "/projects";

    return (
        <div className="col-md-4 clearfix">
            <div className="card mb-2 card-hover-transition">
                <Link to={cardLink}>
                <img className="card-img-top" src={cardImage} alt="Card cap" />
                <div className="card-body card-body-transition">
                    <h4 className="card-title">{cardTitle}</h4>
                    <p className="card-text">{cardDescript}</p>
                </div>
                </Link>
            </div>
        </div>
    ); 
}


function ProjectCarouselSlide(props) {
    return (
        <div className={"carousel-item " + (props.isActive ? "active" : "")}>
            <div className="row">
                <ProjectCarouselCard imgsrc={props.img1} prjNum={props.projs[0]} />
                <ProjectCarouselCard imgsrc={props.img2} prjNum={props.projs[1]}/>
                <ProjectCarouselCard imgsrc={props.img3} prjNum={props.projs[2]}/>
            </div>
        </div>
    );
}


function ProjectCarousel(props) {

    const numCarousel = 9;
    const num_projects = projects.length;
    const displayOrder = [ ...Array(num_projects).keys() ].map( i => i);
    const placeholdOrder = [ ...Array(numCarousel + 1 - num_projects).keys()].map( i=> -1);
    const carouselItems = displayOrder.concat(placeholdOrder);
    const carouselOrder = carouselItems.filter(value => value !== props.curProj);

    return (
        <div className="container my-4">
            <hr />
            <h4>See also</h4>

            <div id="multi-item-example" className="carousel slide carousel-multi-item" data-ride="carousel" data-interval="false">

                {/* <!--Controls--> */}
                <div className="controls-top">
                    <a className="btn-floating btn-carousel-prev" href="#multi-item-example" data-slide="prev"><i className="fa fa-chevron-left"></i></a>
                    <a className="btn-floating btn-carousel-next" href="#multi-item-example" data-slide="next"><i className="fa fa-chevron-right"></i></a>
                </div>
                {/* <!--/.Controls--> */}

                {/* <!--Indicators--> */}
                <ol className="carousel-indicators">
                    <li data-target="#multi-item-example" data-slide-to="0" className="active"></li>
                    <li data-target="#multi-item-example" data-slide-to="1"></li>
                    <li data-target="#multi-item-example" data-slide-to="2"></li>
                </ol>
                {/* <!--/.Indicators--> */}

                {/* <!--Slides--> */}
                <div className="carousel-inner" role="listbox">

                    {/* <!--First slide--> */}
                    <ProjectCarouselSlide 
                        img1="https://mdbootstrap.com/img/Photos/Horizontal/Nature/4-col/img%20(34).jpg"
                        img2="https://mdbootstrap.com/img/Photos/Horizontal/Nature/4-col/img%20(18).jpg"
                        img3="https://mdbootstrap.com/img/Photos/Horizontal/Nature/4-col/img%20(35).jpg"
                        projs={carouselOrder.slice(0, 3)}
                        isActive={true}
                    />

                    {/* <!--Second slide--> */}
                    <ProjectCarouselSlide 
                        img1="https://mdbootstrap.com/img/Photos/Horizontal/City/4-col/img%20(60).jpg"
                        img2="https://mdbootstrap.com/img/Photos/Horizontal/City/4-col/img%20(47).jpg"
                        img3="https://mdbootstrap.com/img/Photos/Horizontal/City/4-col/img%20(48).jpg"
                        projs={carouselOrder.slice(3, 6)}
                        isActive={false}
                    />
                    
                    {/* <!--Third slide--> */}
                    <ProjectCarouselSlide 
                        img1="https://mdbootstrap.com/img/Photos/Horizontal/Food/4-col/img%20(53).jpg"
                        img2="https://mdbootstrap.com/img/Photos/Horizontal/Food/4-col/img%20(45).jpg"
                        img3="https://mdbootstrap.com/img/Photos/Horizontal/Food/4-col/img%20(51).jpg"
                        projs={carouselOrder.slice(6, 9)}
                        isActive={false}
                    />

                </div>
                {/* <!--/.Slides--> */}

            </div>
            {/* <!--/.Carousel Wrapper--> */}
        </div>
    );
}

export default ProjectCarousel;