import React, { useEffect } from "react";

import HomeTitle from "./HomeTitle.jsx";
import HomeEducation from "./HomeEducation.jsx";
import HomeExperience from "./HomeExperience.jsx";
import HomeSkills from "./HomeSkills.jsx";

function HomePage() {

    function updateButtons() {
        let experience_cards = document.querySelectorAll(".experience-content");
        for (let i = 0; i < experience_cards.length; i++) {
          let description_list = experience_cards[i].children[2];
          if (description_list.scrollHeight > description_list.clientHeight) {
            experience_cards[i].lastChild.classList.remove("d-none");
          } else {
            experience_cards[i].lastChild.classList.add("d-none");
          }
        }
      }

    useEffect(() => {
        updateButtons();
        window.addEventListener("resize", updateButtons)
    }, [])

    return (
        <div className="navbar-spacing">
            <HomeTitle />
            <HomeEducation />
            <HomeExperience />
            <HomeSkills />
        </div>
    );
}

export default HomePage;