import React from "react";
import { Link } from "react-router-dom";

import ProjectCarousel from "./ProjectCarousel.jsx";
import projects from "../../projects-content.js";

function ProjectSpotlightImage(props) {
    return (
        <a className="gallery-item" href={props.imagePath}>
            <img src={props.imagePath} alt="placeholder"/>
        </a>
    )
}


function ProjectSpotlight(props) {

    const project = projects[props.project];
    const num_projects = projects.length;
    const prev_project = projects[(props.project - 1 + num_projects) % num_projects];
    const next_project = projects[(props.project + 1) % num_projects];

    return (
    <section className="project-spotlight navbar-spacing">
        <div className="container">
            <div className="project-spotlight-navigation">
                <Link to={prev_project.tile.link} className="previous">&laquo; {prev_project.name}</Link>
                <Link to={next_project.tile.link} className="next">{next_project.name} &raquo;</Link>
            </div>
            <div className="row">
                <div className="col-lg-6 sidebar">
                    <div className="sidebar-content">
                        <h2>{project.name}</h2>
                        <div>
                            <div className="project-date">
                                <i className="far fa-calendar-alt"></i> {project.date}
                            </div>
                            <div className="project-skills">
                                {project.skills.map((skillItem, index) => {
                                    return <span key={index} className="badge rounded-pill project-skill-token"> {skillItem} </span>
                                })}
                            </div>
                        </div>
                        <hr />
                        <h5>Synopsis</h5>
                        {project.synopsis.map((descriptionLine, index)=>{
                            return <p key={index}>{descriptionLine}</p>
                        })}
                        <h5>How it works</h5>
                        {project.description.map((descriptionLine, index) => {
                            return <p key={index}>{descriptionLine}</p>
                        })}
                        <h5>Challenges</h5>
                        {project.challenges.map((descriptionLine, index) => {
                            return <p key={index}>{descriptionLine}</p>
                        })}
                        <h5>Future work</h5>
                        <ul>
                            {project.future.map((descriptionLine, index) => {
                                return <li key={index}>{descriptionLine}</li>
                            })}
                        </ul>
                        <h5>Share + Links</h5>
                        <p>Some links</p>
                    </div>
                </div>
                <div className="col-lg-6 gallery">
                    <div className="gallery-container">
                        
                        {project.images.map((imagePath, index) => {
                            return <ProjectSpotlightImage key={index} imagePath={imagePath} />
                        })}

                    </div>
                </div>
            </div>
        </div>
        <ProjectCarousel curProj={props.project}/>
    </section>
    );
}

export default ProjectSpotlight;